.line-btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  padding: 10px 38px;
  white-space: nowrap;
  font-size: 18px;
  font-weight: 800;
  font-family: "Gilroy", BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: inherit;
  background: 0;
  border: 0;
  border-radius: 0;
  cursor: pointer;
}
.line-btn::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 8px;
  background: #f0c52e;
  transition: 0.2s;
}
.line-btn:hover::before,
.line-btn:focus::before {
  height: 100%;
}
.line-btn:active {
  color: rgba(0,0,0,0.3);
}
.link-btn {
  display: inline-block;
  opacity: 0.5;
  border: 0;
  color: inherit;
  text-decoration: none;
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 0.01em;
  background: 0;
  cursor: pointer;
}
.link-btn:hover {
  opacity: 1;
}
.link-btn:active {
  opacity: 0.2;
}
.text-input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  padding: 0 18px;
  height: 50px;
  border: 3px solid transparent;
  font-family: "Open Sans", BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: #f2f2f2;
  transition: 0.2s;
  transition-property: background, border-coor;
}
.text-input:hover {
  background: #eaeaea;
}
.text-input:focus {
  outline: 0;
  background: #fff;
  border-color: #e5e5e5;
}
.text-input::placeholder {
  opacity: 0.4;
  color: #464646;
}
.text-input_multiline {
  min-height: 156px;
  padding-top: 10px;
  padding-bottom: 10px;
  resize: vertical;
}
.field__label {
  margin-bottom: 8px;
  display: block;
  font-size: 18px;
  font-family: "Gilroy", BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 800;
}
.field__control {
  display: block;
}
.form-grid {
  margin-left: -20px;
  margin-right: -20px;
  display: flex;
  flex-wrap: wrap;
}
.form-grid__cell {
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  padding: 20px;
  flex: 1 1 100%;
}
.form-grid__cell_half {
  width: 50%;
  flex-basis: 50%;
}
.form {
  padding: 80px 100px;
}
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1160px;
  width: 100%;
}
.logo {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;
  font: 800 48px/1 "Gilroy", BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 0.01em;
  transition: 0.2s;
}
.logo:hover {
  color: #464646;
}
.logo:active {
  color: #666;
}
.logo_invert {
  color: #f7e184;
}
.logo_invert:hover {
  color: #f0c52e;
}
.logo_invert:active {
  opacity: 0.3;
}
.search-form {
  position: relative;
  display: flex;
}
.search-form__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 2px;
  margin: auto;
  font-size: 16px;
  width: 1em;
  height: 1em;
  pointer-events: none;
}
.search-form__input {
  box-sizing: border-box;
  padding: 0 40px;
  max-width: 100%;
  flex: 1 1 0%;
  font: inherit;
  border: 0;
  background: 0;
  outline: 0;
}
.search-form__input::placeholder {
  color: inherit;
  opacity: 0.3;
}
.search-form__input:hover::placeholder {
  opacity: 0.6;
}
.search-form__btn {
  position: relative;
  flex: 0 0 auto;
  padding: 14px 20px;
  border: 0;
  opacity: 0;
  visibility: hidden;
  font: inherit;
  background: 0;
  transition: background 0.2s, opacity 0.2s 0.48s, visibility 0.2s 0.48s;
}
.search-form__btn:hover,
.search-form__btn:focus {
  background-color: #000;
  color: #fff;
}
.search-form__btn:active {
  color: rgba(255,255,255,0.3);
}
.search-form__btn-line {
  position: absolute;
  background: #000;
  transition: transform 0.16s;
}
.search-form__btn-line_top,
.search-form__btn-line_bottom {
  left: 0;
  width: 100%;
  height: 2px;
  transform: scaleX(0) scaleY(1);
}
.search-form__btn-line_left,
.search-form__btn-line_right {
  top: 0;
  width: 2px;
  height: 100%;
  transform: scaleX(1) scaleY(0);
}
.search-form__btn-line_bottom {
  bottom: 0;
  transform-origin: 0 0;
  transition-delay: 0s;
}
.search-form__btn-line_right {
  right: 0;
  transform-origin: 0 100%;
  transition-delay: 0.32s;
}
.search-form__btn-line_top {
  top: 0;
  transform-origin: 100% 0;
  transition-delay: 0.16s;
}
.search-form__btn-line_left {
  left: 0;
  transform-origin: 0 0;
  transition-delay: 0s;
}
.search-form::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;
  background: currentColor;
  transform: scaleX(0);
  transform-origin: 0 50%;
  transition: 0.26s 0.48s;
}
.search-form_focus .search-form__btn {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s, 0s, 0s;
}
.search-form_focus::before {
  transform: scaleX(1);
  transition-delay: 0s;
}
.search-form_focus .search-form__btn .search-form__btn-line_bottom,
.search-form_focus .search-form__btn .search-form__btn-line_top {
  transform: scaleX(1) scaleY(1);
}
.search-form_focus .search-form__btn .search-form__btn-line_left,
.search-form_focus .search-form__btn .search-form__btn-line_right {
  transform: scaleX(1) scaleY(1);
}
.search-form_focus .search-form__btn .search-form__btn-line_bottom {
  transition-delay: 0.26s;
}
.search-form_focus .search-form__btn .search-form__btn-line_right {
  transition-delay: 0.26s;
}
.search-form_focus .search-form__btn .search-form__btn-line_top {
  transition-delay: 0.42s;
}
.search-form_focus .search-form__btn .search-form__btn-line_left {
  transition-delay: 0.58s;
}
.dev-link {
