.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.social-list__item {
  margin: 0 8px 8px;
  display: inline-block;
}
.social-list__link {
  display: block;
  opacity: 0.3;
  width: 34px;
  line-height: 34px;
  border: 2px solid;
  text-align: center;
  font-size: 0;
  color: #fff;
  border-radius: 50%;
  transition: 0.16s;
}
.social-list__link:hover {
  opacity: 0.6;
}
.social-list__link:active {
  opacity: 0.2;
}
.social-list__icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 20px;
}
.page-title {
  margin-bottom: 24px;
}
.breadcrumbs {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}
.breadcrumbs__item {
  display: inline-block;
  display: flex;
  align-items: center;
}
.breadcrumbs__item:not(:last-child)::after {
  content: "";
  margin: 0 16px 0 14px;
  transform: rotate(-45deg);
  display: inline-block;
  width: 4px;
  height: 4px;
  border-style: solid;
  border-width: 1px;
  border-top-width: 0;
  border-left-width: 0;
}
.breadcrumbs__link {
  display: inline-block;
  opacity: 0.3;
  font-size: 14px;
  letter-spacing: 0.01em;
  font-family: "Open Sans", BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-decoration: none;
  color: inherit;
  transition: 0.14s;
}
.breadcrumbs__link:hover {
  opacity: 0.6;
}
.breadcrumbs__link:active {
  opacity: 0.1;
}
.sort-type {
  display: flex;
  align-items: baseline;
}
.sort-type__item {
  display: inline-block;
  opacity: 0.3;
  color: inherit;
  text-decoration: none;
  transition: 0.16s;
}
.sort-type__item:hover {
  opacity: 0.6;
}
.sort-type__item:active {
  opacity: 1;
}
.sort-type__item_active {
  opacity: 1;
  pointer-events: none;
}
.sort-type__item:not(:last-child) {
  margin-right: 32px;
}
.sort-order {
  display: flex;
  align-self: stretch;
  flex: 0 0 auto;
}
.sort-order__btn {
  position: relative;
  opacity: 0.2;
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  color: inherit;
  text-decoration: none;
  transition: 0.14s;
}
.sort-order__btn::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: -13px;
  right: -13px;
}
.sort-order__btn::before {
  content: "";
  display: inline-block;
  flex: 0 0 auto;
  border-style: solid;
  border-width: 9px;
  border-left-color: transparent;
  border-right-color: transparent;
}
.sort-order__btn_up::before {
  border-top: 0;
}
.sort-order__btn_down::before {
  border-bottom: 0;
}
.sort-order__btn:hover {
  opacity: 0.4;
}
.sort-order__btn:active {
  opacity: 1;
}
.sort-order__btn_active {
  opacity: 1;
  pointer-events: none;
}
.sort-order__btn + .sort-order__btn {
  margin-left: 26px;
}
