.product-grid {
  margin-left: -20px;
  margin-right: -20px;
  display: flex;
  flex-wrap: wrap;
}
.product-grid__cell {
  margin-bottom: 24px;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
  width: 50%;
  flex: 1 1 calc(50% - 40px);
  display: inline-block;
  vertical-align: top;
}
.product-card {
  display: block;
  text-decoration: none;
  color: inherit;
}
.product-card__head {
  position: relative;
  background: #eee;
}
.product-card__head::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 1px;
  width: 100%;
  height: 100%;
  transform: scaleX(0);
  transform-origin: 0 0;
  pointer-events: none;
  background: #dcdcdc;
}
.product-card__image {
  display: block;
  width: 100%;
  opacity: 0;
  transition: 1.4s;
}
.product-card__tools {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
